import API_PATH from './API_PATH'

const MD_PATH = 'md/'
let MD_VERSION = ''

function formatMdPath (path, page = 'Main', version = '', object = '') {
  if (!path.endsWith('.md')) path += '.md'

  if (page === 'Main') {
    return API_PATH + MD_PATH + path + MD_VERSION
  }
  if (page === 'DocsVersions') {
    return API_PATH + MD_PATH + 'version-' + path + MD_VERSION
  }
  if (page === 'DocsObject') {
    return API_PATH + MD_PATH + 'object-v' + version + '-' + path + MD_VERSION
  }
  if (page === 'DocsMethod') {
    return API_PATH + MD_PATH + 'method-v' + version + '-' + object + '-' + path + MD_VERSION
  }
}

function generateMdPath (vm, page = 0) {
  const mdFileNotFound = 'file_error_404'
  const documentation = vm.$store.getters.documentation
  MD_VERSION = '?v=' + documentation['project-version']
  const toMain = () => {
    vm.$route.push({ name: 'Main' })
    return false
  }

  try {
    const version = (documentation.content.versions[vm.$route.params.version]?.filename[0] ?? '')
    const object = (documentation.content.versions[vm.$route.params.version]?.objects[vm.$route.params.object]?.filename[0] ?? '')
    switch (vm.$route.name) {
      case 'Main':
        return formatMdPath(documentation['project-path-content'].main[page], 'Main')
      case 'DocsVersions':
        if (!vm.$route?.params?.version) return toMain()
        return formatMdPath(documentation.content.versions[vm.$route.params.version].filename[page], 'DocsVersions')
      case 'DocsObject':
        if (!vm.$route?.params?.version) return toMain()
        if (!vm.$route?.params?.object) return toMain()
        return formatMdPath(documentation.content.versions[vm.$route.params.version].objects[vm.$route.params.object].filename[page], 'DocsObject', version)
      case 'DocsMethod':
        if (!vm.$route?.params?.version) return toMain()
        if (!vm.$route?.params?.object) return toMain()
        if (!vm.$route?.params?.method) return toMain()
        return formatMdPath(documentation.content.versions[vm.$route.params.version].objects[vm.$route.params.object].methods[vm.$route.params.method].filename[page], 'DocsMethod', version, object)
      default:
        return mdFileNotFound
    }
  } catch {
    return mdFileNotFound
  }
}

export default generateMdPath
