<template>
  <nav class="navbar sticky-top py-2">
    <div class="container justify-content-center">
      <router-link class="navbar-brand h1 mx-0" :to="{ name: 'Main' }">
        <span @click.prevent.stop="goToProject()" class="project-name">{{docData['project-name']}}</span>
        <span class="project-version">{{docData['project-version'] ? 'v' + docData['project-version'] : ''}}</span>
      </router-link>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'TheHeader',
  computed: {
    docData () {
      return this.$store.getters.documentation
    }
  },
  methods: {
    goToProject () {
      window.open(this.docData['project-path'], '_blank')
    }
  }
}
</script>

<style scoped>
.project-name {
  color: white;
  padding-right: 0.5em;
}
</style>
