<template>
  <div class="page">
    <Markdown :source="mdCode"></Markdown>
  </div>
</template>

<script>
import Markdown from 'vue3-markdown-it'
import Api from '../api'
import formatMdVar from '../api/formatMdVars'
import generateMdPath from '../api/generateMdPath'

export default {
  name: 'Main',
  components: {
    Markdown
  },
  data () {
    return {
      mdCode: '# Page: Main'
    }
  },
  mounted () {
    this.initMdFile()
  },
  methods: {
    initMdFile () {
      const mdPath = generateMdPath(this)
      Api.mdGetMain(mdPath).then(({ data }) => {
        this.mdCode = formatMdVar(this.$store.getters.documentation.vars, data)
      })
    }
  }
}
</script>
