<template>
  <div class="sidebar">
    <router-link class="sidebar__title sidebar__link" :to="{ name: 'Main' }">Меню</router-link>
    <span v-for="(item, key) in docData" :key="key" class="sidebar__item">
      <router-link class="sidebar__link" :to="`/v${key}`">{{item.label}}</router-link>
      <template v-if="$route.params.version === key">
        <span v-for="(itemObject, keyObject) in docData[key].objects" :key="keyObject" class="sidebar__item sidebar__item_object">
          <router-link class="sidebar__link" :to="`/v${key}/${keyObject}`">{{itemObject.label}}</router-link>
          <template v-if="$route.params.object === keyObject">
            <span v-for="(itemMethod, keyMethod) in docData[key].objects[keyObject].methods" :key="keyMethod" class="sidebar__item sidebar__item_method">
              <router-link class="sidebar__link" :to="`/v${key}/${keyObject}.${keyMethod}`">{{itemMethod.label}}</router-link>
            </span>
          </template>
        </span>
      </template>
    </span>
  </div>
</template>

<script>
export default {
  name: 'TheSidebar',
  data () {
    return {
    }
  },
  computed: {
    docData () {
      return this.$store.getters.documentation.content.versions
    }
  }
}
</script>

<style scoped>
.sidebar {
  display: block;
  width: 100%;
  height: 100%;
  background-color: white;
  padding: 1em 1.5em;
  border: 1px solid #fcfcfc;
  border-radius: 0.5em;
}
.sidebar__title {
  display: block;
  text-align: center;
  font-size: x-large;
  margin-bottom: 0.5em;
}
.sidebar__item {
  display: block;
  text-align: center;
  font-size: large;
  padding: 0.5em 1em;
  background: #F0F0F0;
  border: 1px solid #fcfcfc;
  border-radius: 0.5em;
  margin-bottom: 1em;
}
.sidebar__item:last-child {
  margin-bottom: 0em;
}

.sidebar__link {
  display: block;
  color: black;
  text-decoration: none;
  cursor: pointer;
  margin-bottom: 0.5em;
}
.sidebar__item_object {
  background: white;
  margin-bottom: 0.5em;
}

.sidebar__item_method {
  background: #F0F0F0;
  margin-bottom: 0.5em;
}
</style>
