import { createRouter, createWebHashHistory } from 'vue-router'
import Main from '../views/Main.vue'

const routes = [
  {
    path: '/',
    name: 'Main',
    component: Main
  },
  {
    path: '/v:version',
    name: 'DocsVersions',
    component: () => import(/* webpackChunkName: "docs" */ '../views/DocsVersions.vue')
  },
  {
    path: '/v:version/:object',
    name: 'DocsObject',
    component: () => import(/* webpackChunkName: "docs" */ '../views/DocsObject.vue')
  },
  {
    path: '/v:version/:object.:method',
    name: 'DocsMethod',
    component: () => import(/* webpackChunkName: "docs" */ '../views/DocsMethod.vue')
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'PageNotFound',
    component: import(/* webpackChunkName: "error" */ '../views/PageNotFound.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
