<template>
  <div class="layout">
    <div v-if="loader" class="container text-center">
      <h1>Загрузка данных</h1>
    </div>
    <div v-if="!loader" class="layout__sidebar">
      <the-sidebar></the-sidebar>
    </div>
    <div v-if="!loader" class="layout__content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import Api from '../api'
import TheSidebar from './TheSidebar.vue'
export default {
  components: { TheSidebar },
  name: 'TheLayout',
  data () {
    return {
      loader: true
    }
  },
  mounted () {
    Api.init().then(({ data }) => {
      this.$store.commit('documentation', data)
      this.loader = false
    })
  }
}
</script>
